.serialDisplay {
    background-color: black;
    background-image: radial-gradient(rgba(0, 150, 0, 0.75), black 120%);
    height: 30vh;
    margin: 0;
    overflow: hidden;
    padding: 2rem;
    overflow: auto;
    color: white;
    font: 1.1rem Inconsolata, monospace;
    text-shadow: 0 0 5px #C8C8C8;
  }
  .serialDisplay::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    /* width: 100vw; */
    height: 30vh;
    background: repeating-linear-gradient(0deg, rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.15) 1px, transparent 1px, transparent 2px);
    pointer-events: none;
  }
  
  .serialDisplay::selection {
    background: #0080FF;
    text-shadow: none;
  }
  
  pre {
    margin: 0;
  }

  p {
    margin-bottom: .05rem;
  }